import React from 'react'
import './projectContainer.scss'

const ProjectContainer = ({alt}) => {
    if (alt === "left") {
        return <Left />
    } else {
        return right
    }
    
}

const Left = () => (
    <div>
        Left
    </div>
)

const right = () => (
    <div>
        right
    </div>
)


export default ProjectContainer